// Component: Reservation Box

.reservation-box {
	.date {
		margin-bottom: $gap-size-20;
		text-align: center;
		.inp-date {
			margin: 0 10px;
			width: 70%;
			max-width: 180px; } }
	.timetable {
		.time {
			margin-bottom: 5px;
			span {
				display: inline-block;
				width: calc(100%/23);
				text-align: center; } }
		.boxes {
			padding-left: 10px;
			padding-right: 10px;
			.box {
				display: inline-block;
				width: calc(100%/11);
				height: 30px;
				border-right: 1px solid #000;
				&:first-of-type {
					border-left: 1px solid #000; }
				.half {
					display: inline-block;
					width: 50%;
					height: 30px;
					background-color: $color-green;
					&.unavailable {
						background-color: $color-red; }
					&.selected {
						background-color: $color-primary; } } } } }
	.clock-timepicker {
		width: 100%; }
	.time-start {
		display: inline-block;
		width: 50%;
		padding-right: $gap-size-20 / 2; }
	.time-end {
		display: inline-block;
		width: 50%;
		padding-left: $gap-size-20 / 2; } }
