// Component: Search Box

.search-box {
	width: 100%;
	max-width: 700px;
	margin-left: auto;
	margin-right: auto;
	.row {
		div.input-with-icon {
			margin-right: 1em;
			margin-bottom: 1em;
			&:last-child {
				margin-right: 0; }
			i {
				top: 17px; } } }
	input, select {
		font-family: $font-styled;
		font-size: 1.2em;
		text-align: center; }
	.inp-flex {
		flex: 1; }
	#inp-date {
		width: 30%; }
	#inp-seat {
		width: 20%; }
	.inp-50 {
		width: 50%; }
	.tags {
		margin-bottom: $gap-size-20;
		text-align: center;
		font-family: $font-styled;
		label {
			margin-right: 1em;
			&:last-of-type {
				margin-right: 0; } } } }

@media screen and (max-width: $container-steep-width - 1) {
	.search-box {
		.flex {
			display: block;
			div {
				width: 100%;
				margin-right: 0 !important; } }
		#inp-date, #inp-seat {
			width: 100%; } } }
